import * as React from 'react';

interface IHomeState {
    monkey: any;
}

class Home extends React.Component<{}, IHomeState> {
    constructor(props: any) {
        super(props);
        this.state = {
            monkey: {
                info: {
                    name: 'monkey',
                    age: 12
                }
            }
        }
    }

    onToApp = () => {
        window.location.href = '#/blog?app=PAJK&outBizType=PAJK';
    }

    onChangeAge = () => {
        const { monkey } = this.state;
        const { info } = monkey
        // info.age = 18;
        console.log("monkey===monkey", monkey === this.state.monkey);
        this.setState({
            monkey
        }, () => {
            console.log("changedState===>", this.state.monkey);
        });
    }
    render() {
        console.log('render-info==>', this.state);
        return <div>
            <button onClick={this.onToApp}>跳转中间页</button>
            <button onClick={this.onChangeAge}>更改年龄</button>
            <div>
                <span>姓名：</span>
                <span>
                    {this.state.monkey.info.name}
                </span>
            </div>
            <div>
                <span>年纪：</span>
                <span>
                    {
                        this.state.monkey.info.age
                    }
                </span>
            </div>
        </div>
    }
}

export default Home;